<template>
  <div class="notic" v-show="show">
    <main>
      <div class="message">
        <h2>{{ noticeData.title }}</h2>
        <p>{{ noticeData.contents }}</p>
      </div>
      <i class="close wave" @click="hide"></i>
    </main>
  </div>
</template>

<script>
export default {
  model: {
    prop: 'show',
    event: 'change'
  },
  props: {
    show: Boolean,
    noticeData: {
      type: [ Object, Array ],
      default () {
        return {
          title: '加载中',
          contents: '加载中'
        }
      }
    }
  },
  created () {
  },
  methods: {
    hide () {
      this.$emit('change', false)
    }
  }
}
</script>

<style lang="scss" scoped>
 @import '@/style/mixin.scss';
 .notic {
  position: fixed;
  top: 0;
  left: 0;
  @include wh(100%);
  background: rgba(0, 0, 0, 0.64);
  @include fb(center-row);
  main {
    @include wh(5.62rem, 7.39rem);
    @include bg('~assets/notice/bg.png');
    position: relative;
    .close{
      position: absolute;
      bottom: -16%;
      left: 50%;
      transform: translateX(-50%);
      @include wh(.66rem);
      @include bg('~assets/notice/close.png');
    }
    .message {
      @include center;
      top: 55%;
      @include wh(4rem);
      h2 {
        text-align: center;
        @include space(padding, 0.3rem);
        @include sc(0.36rem, #000000, bold);
      }
      p {
        text-indent: 1em;
      }
    }
  }
 }
</style>
