<template>
  <div class="homepage">
    <header>
      <j-swiper
        :list="list"
        height="100%"
        :interval="3000"
        :duration="366"
        :base-url="$baseUrl"
        :auto="true">
      </j-swiper>
    </header>
    <main>
      <nav>
        <span>热门产品</span>
        <span @click="test">最新</span>
        <span>状态</span>
      </nav>
      <div class="content" ref="homePage">
        <ul>
          <li class="list" v-waves
            v-for="(obj, key) in dataList"
            :key='key'
            @click="jump(obj)">
            <img :src="obj.img" alt="">
            <ul>
              <li>
                <span>{{ obj.cname }}</span>
                <span>{{ obj.ename }}</span>
              </li>
              <li><span :style="{backgroundColor:obj.sorted}">{{obj.price}}</span></li>
              <li class="right-arrow"><span :style="obj.closes=='休市'?'background:#3a8ee6':''">{{ obj.closes }}</span></li>
            </ul>
          </li>
        </ul>
      </div>
    </main>
    <notice v-model="noticeShow" :notice-data="noticeData"/>
  </div>
</template>

<script>
// @ is an alias to /src
import { getSalesman } from '@/utils/auth'
import JSwiper from '@/components/JSwiper'
import notice from './components/notice'
import BScroll from 'better-scroll'
import { mapActions } from 'vuex'

export default {
  name: 'homepage',
  components: {
    JSwiper,
    notice
  },
  data () {
    return {
      dataList: [],
      timer: '',
      scroll: '',
      noticeShow: false,
      noticeData: {},
      list: []
    }
  },
  created () {
    this.init()
    this.circulation()
    for (let i = 1; i < 6; i++) {
      i !== 13 ? this.list.push({
        img: `assets/home/${i}.png`
      }) : this.list.push({
        img: `assets/home/${i}.png`,
        fallbackImg: `assets/home/1.png`
      })
    }
  },
  methods: {
    ...mapActions([
      'setAudio'
    ]),
    test () {
      this.$store.getters.audioSwitch === 'on' ? this.setAudio('off') : this.setAudio('on')
    },
    init (loading) {
      this.request({
        url: '/index',
        method: 'get'
      }).then(res => {
        if (res.code === 1) {
          this.dataList = res.data
        }
      })
      this.request({
        url: '/new-notices',
        method: 'get'
      }).then(res => {
        if (res.code === 1) {
          this.noticeData = res.data
          this.noticeShow = true
        }
      })
    },
    circulation () {
      this.timer = setInterval(() => {
        this.init(false)
      }, 3000)
    },
    jump (obj) {
      if (obj.closes === '休市' && getSalesman() === 10) {
        this.$toast('产品休市中')
      } else if (obj.closes === '开市' || getSalesman() === 2) {
        this.$router.push({ name: 'details', params: obj })
      }
    }
  },
  mounted () {
    this.$nextTick().then(res => {
      this.scroll = new BScroll(this.$refs.homePage, {
        scrollY: true,
        scrollX: true,
        momentum: true,
        bounce: true,
        click: true
      })
    })
  },
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
